.homepage-container>*:not(:first-child){margin-block-end:40px}.homepage-container .hero-text{text-align:center;color:#fff;margin-block-end:70px;margin-block-start:70px}.homepage-container .hero-text .main-heading{font-family:audioWide;color:#187bcf;font-size:2.5rem;text-transform:uppercase}.homepage-container .hero-text :nth-child(2){font-size:1.3rem}.homepage-container .hero-text>*:not(:last-child){margin-block-end:20px}.homepage-container .hero-btn{font-family:inherit;font-size:1.1rem;color:#fff;position:relative;padding:10px 20px;border:1px #fff solid;border-radius:8px;transition:padding-inline-end,.5s}.homepage-container .hero-btn:hover{padding-inline-end:40px}.homepage-container .hero-btn:hover::after{content:"  →";position:absolute;right:12px;bottom:2px;font-size:1.7rem}.homepage-container .heading{font-size:1.4rem;background-color:#ffffff3d;border-radius:8px;padding-inline-start:5px;padding-inline-end:5px;padding-block-start:3px;padding-block-end:3px;margin-block-end:10px;margin-inline-start:10px;margin-inline-end:10px}